
import { __ } from '@wordpress/i18n';
import { useState, useEffect } from '@wordpress/element';
import { Button } from '@wordpress/components';
import { Modal } from '@wordpress/components';
import produce from 'immer';

import { icons } from './Const/icons';
import Settings from './Settings';
import Lightbox from './Lightbox';
import Style from './Style';
import ContentArea from './Const/Components/ContentArea';

import BPLSDK from '../bplugins_sdk/src/components/v1/BPLSDK';

const Edit = props => {
	const { className, attributes, setAttributes, clientId, isSelected } = props;

	const { lightboxType } = attributes

	useEffect(() => { clientId && setAttributes({ cId: clientId.substring(0, 10) }); }, [clientId]); // Set & Update clientId to cId

	const [activeIndex, setActiveIndex] = useState(0);

	const updateItem = (type, index, val, childType = false) => {
		const newItem = produce(attributes.items, draft => {
			if (childType) {
				draft[index][type][childType] = val;
			} else {
				draft[index][type] = val;
			}
		});
		setAttributes({ items: newItem });
	}

	return !lightboxType ?
		<div className='lbbLightBoxPlaceholder'>
			<h3>{icons.lightbox}{__('Select a Lightbox Type', 'lightbox')}</h3>

			<div className='lightboxlayout'>
				<Button showTooltip label={__('Gallery', 'lightbox')} tooltipPosition='top' icon="format-gallery" onClick={() => { setAttributes({ lightboxType: "gallery" }); }}>Gallery</Button>

				<Button showTooltip label={__('Button', 'lightbox')} icon="button" tooltipPosition='top'
					onClick={() => { setAttributes({ lightboxType: "button" }); }}>Button</Button>
			</div>
		</div>
		: <>
			<Settings attributes={attributes} setAttributes={setAttributes} clientId={clientId} activeIndex={activeIndex} setActiveIndex={setActiveIndex} />
			<BPLSDK setAttributes={setAttributes} />
			<div className={className} id={`lbbLightBox-${attributes.cId}`}>
				<Style attributes={attributes} clientId={attributes.cId} />
				{!isSelected && <div className='lbbPopProtect'></div>}
				<Lightbox ContentArea={ContentArea} Modal={Modal} clientId={clientId} isSelected={isSelected} attributes={attributes} isBackend={true} custom={{ updateItem }} />
			</div>
		</>
};
export default Edit;