import { __ } from '@wordpress/i18n';
import { useState } from '@wordpress/element';
import produce from 'immer';
import { InspectorControls } from '@wordpress/block-editor';
import { PanelBody, PanelRow, TabPanel, SelectControl, RangeControl, Button, Dashicon, __experimentalUnitControl as UnitControl, TextControl, __experimentalBoxControl as BoxControl, __experimentalBorderControl as BorderControl, ToggleControl, } from '@wordpress/components';

// Settings Components
import { Label, BtnGroup, IconControl, BDevice, Typography, ColorsControl, HelpPanel } from '../../Components';

import { layoutOpt, generalStyleTabs, lbType, btnTypeOpt, pxUnit, perUnit, emUnit, aligns, btnSopt, thumbTypeOpt, wheelOpt } from './Const/options';
import ItemSettings from './Const/Components/ItemSettings';

const dummyImage = "https://bblockswp.com/wp-content/demo/img/green-hill.jpg";

const Settings = ({ attributes, setAttributes }) => {
	const { lightboxType, items, layout, columns, columnGap, rowGap, galleryIcon, popupOptions, popupIconLeft, popupIconMiddle, popupIconRight, thumb, button, btnTypo, btnWidth, btnHeight, btnAlign, btnType, btnStyle, btnColors, btnHovColors, btnPadding, btnBorder, btnRadius, btnMargin, btnSpaceBetween } = attributes;
	const [device, setDevice] = useState('desktop');

	// button Update
	const updateAttr = (attr, type, val) => {
		const newAttr = produce(attributes[attr], draft => {
			draft[type] = val;
		});
		setAttributes({ [attr]: newAttr });
	}

	/*
	 *
	 * Add Item
	 * 
	*/
	const addItem = () => {
		setAttributes({
			items: [
				...items,
				{
					type: "image",
					thumbnail: dummyImage,
					content: dummyImage
				}
			]
		})
	}

	return <>
		<InspectorControls>
			<TabPanel className='bPlTabPanel lbbTabPanel' activeClass='activeTab' tabs={generalStyleTabs} >{tab => <>
				{'general' === tab.name && <>

					<HelpPanel slug="lightbox-block" docsLink="https://bplugins.com/docs/lightbox-block" />

					{/* Select Lightbox Type  */}
					<PanelBody className='bPlPanelBody' title={__('Select Lightbox Type', 'lightbox')} initialOpen={false}>
						<SelectControl labelPosition="side" label={__('Lightbox Type', 'lightbox')} value={lightboxType} options={lbType} onChange={val => { setAttributes({ lightboxType: val }) }} />

						{/* Check button and 1 gether than  */}
						{'button' == lightboxType && items.length > 1 && <>
							<Button className='removeItem removeAllitem' onClick={() => setAttributes({ items: [items[0]] })}> <Dashicon icon='trash' size={18} />{__('Delete Gallery Data', 'lightbox')}</Button>
						</>}
					</PanelBody>

					<PanelBody className='bPlPanelBody addRemoveItems' title={'button' === lightboxType ? 'Lightbox Item' : 'Lightbox Items'}>
						{/* check button then single item and gallery type than Items  */}
						{'button' === lightboxType ? <ItemSettings items={items} item={items[0]} index={0} attributes={attributes} setAttributes={setAttributes} /> :
							items.map((item, index) => {
								return <PanelBody key={index} className='bPlPanelBody editItem' title={`Item ${index + 1}`} initialOpen={false}>
									<ItemSettings items={items} item={item} index={index} attributes={attributes} setAttributes={setAttributes} />
								</PanelBody>
							})
						}

						{/* lightbox type button Hide Add button  */}
						{'button' !== lightboxType && <div className='addItem'>
							<Button onClick={addItem}><Dashicon icon='plus' size={23} />{__('Add New Item', 'lightbox')}</Button>
						</div>}
					</PanelBody>

					<PanelBody title={__('Popup Options', 'lightbox')} className="bPlPanelBody" initialOpen={false}>

						<ToggleControl label={__('Mixed All Content', 'lightbox')} checked={popupOptions?.mixAllData} onChange={(val) => {
							setAttributes({ popupOptions: { ...popupOptions, mixAllData: val } });
						}} />

					</PanelBody>

					{/* lightbox type button than Hide Layout Panel */}
					{'button' !== lightboxType &&
						<PanelBody title={__('Layout Settings', 'lightbox')} className="bPlPanelBody" initialOpen={false} >

							<ToggleControl label={__('Hide Type Icons', 'lightbox')} checked={galleryIcon} onChange={(val) => { setAttributes({ galleryIcon: val }); }} />

							<SelectControl label={__('Layout', 'lightbox')} className='mt10' labelPosition="side" value={layout} options={layoutOpt} onChange={(val) => {
								setAttributes({ layout: val })
							}} />

							{/* column Gap  */}
							<UnitControl className='mt20' label={__('Column Gap:', 'lightbox')} labelPosition='left' value={columnGap} onChange={val => setAttributes({ columnGap: val })} units={[pxUnit(30), perUnit(3), emUnit(2)]} isResetValueOnUnitChange={true} />

							{/* row Gap  */}
							<UnitControl className='mt20' label={__('Row Gap:', 'lightbox')} labelPosition='left' value={rowGap} onChange={val => setAttributes({ rowGap: val })} units={[pxUnit(40), perUnit(3), emUnit(2.5)]} isResetValueOnUnitChange={true} />

							{/* column define option  */}
							<PanelRow className='mt20'>
								<Label mt='0'>{__('Columns:', 'lightbox')}</Label>
								<BDevice device={device} onChange={val => setDevice(val)} />
							</PanelRow>
							<RangeControl value={columns[device]} onChange={val => { setAttributes({ columns: { ...columns, [device]: val } }) }} min={1} max={6} step={1} beforeIcon='grid-view' />
						</PanelBody>
					}

					{/* lightbox type button than show button settings  */}
					{'button' === lightboxType && <PanelBody title={__('Button Settings', 'lightbox')} className="bPlPanelBody" initialOpen={false} >

						<Label>{__('Text', 'lightbox')} </Label>
						<TextControl value={button.text} labelPosition='top' onChange={val => updateAttr('button', 'text', val)} />

						<IconControl className='mt20' icon={button.icon}
							onChange={val => updateAttr('button', 'icon', val)} isSize={false} isColor={false} />
					</PanelBody>}
					{/* Pro feature  */}
					{/* <PanelBody title={__('Pop Icon show', 'lightbox')} className="bPlPanelBody" initialOpen={false}>

						<PanelBody title={__('Left Popup Icon', 'lighltbox')} className="bPlPanelBody" initialOpen={false} >
							<ToggleControl className='mt10' label={__('Info bar', 'lightbox')} checked={popupIconLeft?.infobar} onChange={(val) => { setAttributes({ popupIconLeft: { ...popupIconLeft, infobar: val } }); }} />
						</PanelBody>

						<PanelBody title={__('Middle Popup Icon', 'lighltbox')} className="bPlPanelBody" initialOpen={false} >
							<ToggleControl className='mt10' label={__('Zoom In', 'lightbox')} checked={popupIconMiddle?.zoomIn} onChange={(val) => { setAttributes({ popupIconMiddle: { ...popupIconMiddle, zoomIn: val } }); }} />

							<ToggleControl className='mt10' label={__('Zoom Out', 'lightbox')} checked={popupIconMiddle?.zoomOut} onChange={(val) => { setAttributes({ popupIconMiddle: { ...popupIconMiddle, zoomOut: val } }); }} />

							<ToggleControl className='mt10' label={__('Toggle1to1', 'lightbox')} checked={popupIconMiddle?.toggle1to1} onChange={(val) => { setAttributes({ popupIconMiddle: { ...popupIconMiddle, toggle1to1: val } }); }} />

							<ToggleControl className='mt10' label={__('RotateCCW', 'lightbox')} checked={popupIconMiddle?.rotateCCW} onChange={(val) => { setAttributes({ popupIconMiddle: { ...popupIconMiddle, rotateCCW: val } }); }} />

							<ToggleControl className='mt10' label={__('RotateCW', 'lightbox')} checked={popupIconMiddle?.rotateCW} onChange={(val) => { setAttributes({ popupIconMiddle: { ...popupIconMiddle, rotateCW: val } }); }} />

							<ToggleControl className='mt10' label={__('FlipX', 'lightbox')} checked={popupIconMiddle?.flipX} onChange={(val) => { setAttributes({ popupIconMiddle: { ...popupIconMiddle, flipX: val } }); }} />

							<ToggleControl className='mt10' label={__('FlipY', 'lightbox')} checked={popupIconMiddle?.flipY} onChange={(val) => { setAttributes({ popupIconMiddle: { ...popupIconMiddle, flipY: val } }); }} />

							<ToggleControl className='mt10' label={__('Fullscreen', 'lightbox')} checked={popupIconMiddle?.fullscreen} onChange={(val) => { setAttributes({ popupIconMiddle: { ...popupIconMiddle, fullscreen: val } }); }} />
						</PanelBody>

						<PanelBody title={__('Right Popup Icon', 'lightbox')} className="bPlPanelBody" initialOpen={false}>
							<ToggleControl className='mt10' label={__('Slide Shows', 'lightbox')} checked={popupIconRight?.slideshow} onChange={(val) => { setAttributes({ popupIconRight: { ...popupIconRight, slideshow: val } }); }} />

							<ToggleControl className='mt10' label={__('Thumbs', 'lightbox')} checked={popupIconRight?.thumbs} onChange={(val) => { setAttributes({ popupIconRight: { ...popupIconRight, thumbs: val } }); }} />

							<ToggleControl className='mt10' label={__('Close', 'lightbox')} checked={popupIconRight?.close} onChange={(val) => { setAttributes({ popupIconRight: { ...popupIconRight, close: val } }); }} />

							<ToggleControl className='mt10' label={__('Facebook Share', 'lightbox')} checked={popupIconRight?.facebook} onChange={(val) => { setAttributes({ popupIconRight: { ...popupIconRight, facebook: val } }); }} />

							<ToggleControl className='mt10' label={__('Twitter Share', 'lightbox')} checked={popupIconRight?.twitter} onChange={(val) => { setAttributes({ popupIconRight: { ...popupIconRight, twitter: val } }); }} />

							<ToggleControl className='mt10' label={__('Download', 'lightbox')} checked={popupIconRight?.download} onChange={(val) => { setAttributes({ popupIconRight: { ...popupIconRight, download: val } }); }} />
						</PanelBody>

					</PanelBody> */}
					{/* Thumbnails  */}
					{/* <PanelBody title={__("Thumbnails", "lightbox")} className="bPlPanelBody" initialOpen={false} >

						<SelectControl labelPosition="side" className='mt10' label={__('Type', 'lightbox')} value={thumb?.type} options={thumbTypeOpt} onChange={val => setAttributes({ thumb: { ...thumb, type: val } })} />

						<SelectControl labelPosition="side" className='mt10' label={__('Wheel', 'lightbox')} value={thumb?.thumb} options={wheelOpt} onChange={val => setAttributes({ thumb: { ...thumb, wheel: val } })} />

						<ToggleControl className='mt10' label={__('Show On Start In', 'lightbox')} checked={thumb?.showOnStart} onChange={(val) => { setAttributes({ thumb: { ...thumb, showOnStart: val } }); }} />

					</PanelBody> */}

				</>}

				{/* lightbox Type button than show button style  */}
				{'style' === tab.name && <>
					{'button' === lightboxType &&
						<PanelBody PanelBody className='bPlPanelBody lbbPanelBody' title={__('Button', 'lightbox')} initialOpen={true}>
							{/* button width height  */}
							<div className="lbbWHControl">
								{/* Widht  */}
								<UnitControl label={__('Width', 'lightbox')} className='' value={btnWidth}
									onChange={(val) => { setAttributes({ btnWidth: val }) }} units={[pxUnit(10), perUnit(100)]} onUnitChange="" isUnitSelectTabbable />
								{/* Height 	 */}
								<UnitControl label={__('Height', 'lightbox')} className='' value={btnHeight}
									onChange={(val) => { setAttributes({ btnHeight: val }) }} units={[pxUnit(10), perUnit(100)]} onUnitChange="" isUnitSelectTabbable />
							</div>

							{/* Button Style  */}
							<PanelRow className='mt20 mb20'>
								<Label mt='0' mb='0'>{__('Style:', 'lightbox')}</Label>
								<BtnGroup value={btnStyle}
									onChange={val => {

										if ('fill' === val) {
											setAttributes({
												btnStyle: val,
												btnColors: { color: '#fff', bg: '#4527a4', styles: `color: #fff; background: #4527a4` }
											})
										}

										if ('outline' === val) {
											setAttributes({
												btnStyle: val,
												btnBorder: { color: "#ddd", style: 'solid', width: '1px' },
												btnColors: { color: '#4527a4', bg: '#fff0', styles: `color: #4527a4; background: #fff0` }
											})
										}
									}
									}
									options={btnSopt} isIcon={false} />
							</PanelRow>

							<SelectControl label={__('Type', 'lightbox')} labelPosition="side" value={btnType} options={btnTypeOpt} onChange={val => {
								const radius = val === 'circle' ? '50%' : '3px';
								const pillRadius = val === 'pill' ? '100px' : '3px';
								const pillTBPadding = val === 'pill' ? '10px' : '8px';
								const pillLRBPadding = val === 'pill' ? '20px' : '15px';
								const defaultTBPadding = val === 'default' ? '8px' : '8px';
								const defaultRBPadding = val === 'default' ? '15px' : '15px';

								if (val === 'default') {
									setAttributes({
										btnType: val,
										btnWidth: 'auto',
										btnHeight: 'auto',
										btnRadius: { "top": radius, "right": radius, "bottom": radius, "left": radius },
										btnPadding: { "top": defaultTBPadding, "right": defaultRBPadding, "bottom": defaultTBPadding, "left": defaultRBPadding }
									})
								}

								if (val === 'circle') {
									setAttributes({
										btnType: val,
										btnWidth: '100px',
										btnHeight: '100px',
										btnRadius: { "top": radius, "right": radius, "bottom": radius, "left": radius },
										btnPadding: { "top": pillTBPadding, "right": pillLRBPadding, "bottom": pillTBPadding, "left": pillLRBPadding }
									})
								}

								if (val === 'fullWidth') {
									setAttributes({
										btnType: val,
										btnWidth: '100%',
										btnHeight: 'auto',
										btnRadius: { "top": radius, "right": radius, "bottom": radius, "left": radius },
									})
								}

								if (val === 'pill') {
									setAttributes({
										btnType: val,
										btnWidth: 'auto',
										btnHeight: 'auto',
										btnRadius: { "top": pillRadius, "right": pillRadius, "bottom": pillRadius, "left": pillRadius },
										btnPadding: { "top": pillTBPadding, "right": pillLRBPadding, "bottom": pillTBPadding, "left": pillLRBPadding }
									})
								}
							}} />

							<PanelRow className='mt20'>
								<Label mt='0' mb='0'>{__('Align:', 'lightbox')}</Label>
								<BtnGroup value={btnAlign} onChange={val => setAttributes({ btnAlign: val })} options={aligns} isIcon={true} />
							</PanelRow>

							{/* Typography  */}
							<Typography className='' label={__('Typography:', 'lightbox')} value={btnTypo} isFamily={false} onChange={val => { setAttributes({ btnTypo: val }) }} defaults={{ fontSize: 25 }} produce={produce} />

							{/* color */}
							<ColorsControl className='' label={__('Colors', 'lightbox')} value={btnColors} onChange={val => setAttributes({ btnColors: val })} defaults={{ color: '#333', bg: '#fff' }} />

							{/* Hover color  */}
							<ColorsControl label={__('Hover Colors', 'lightbox')} className="mb15" value={btnHovColors} onChange={val => setAttributes({ btnHovColors: val })} defaults={{ color: '#333', bg: '#fff' }} />

							{/* Padding */}
							<BoxControl label={__("Padding", 'lightbox')} values={btnPadding} onChange={val => setAttributes({ btnPadding: val })}
							/>

							{/* button border  */}
							<Label>{__('Border', 'lightbox')}</Label>
							<BorderControl className="mb20" value={btnBorder} onChange={(value) => setAttributes({ btnBorder: value })}
							/>

							{/* border radius  */}
							<BoxControl label={__('Border Radius', 'lightbox')} values={btnRadius} onChange={val => setAttributes({ btnRadius: val })}
							/>

							{/* margin */}
							<BoxControl label={__('Margin', 'lightbox')} values={btnMargin} onChange={val => setAttributes({ btnMargin: val })}
							/>

							{/* Icon Space */}
							{button.icon.class &&
								<UnitControl label={__('Space between icon & text', 'lightbox')} className='' value={btnSpaceBetween}
									onChange={(val) => {
										setAttributes({
											btnSpaceBetween: val
										})
									}} units={[pxUnit(10)]} />
							}

						</PanelBody>
					}
				</>}

			</>}</TabPanel>

		</InspectorControls>

	</>;
};
export default Settings;