import { __ } from '@wordpress/i18n';
import produce from 'immer';

import { PanelRow, SelectControl, Button, Dashicon, TextControl } from '@wordpress/components';

import { contentType } from '../options';
import { icons } from '../icons';

import { Label, InlineMediaUpload } from '../../../../Components';
// Item Settings

const ItemSettings = ({ attributes, setAttributes, items, item, index }) => {
    const { lightboxType } = attributes;
    const { type, thumbnail, content, caption } = item;

    const updateItem = (type, index, val, childType = false) => {
        const newItem = produce(items, draft => {
            if (childType) {
                draft[index][type][childType] = val;
            } else {
                draft[index][type] = val;
            }
        });
        setAttributes({ items: newItem });
    }

    /*
     *
     * Remove Item
     * 
    */
    const removeItem = (index) => {
        const removeItem = [...items];
        removeItem.splice(index, 1);
        setAttributes({ items: removeItem });
    }

    /*
     *
     * Item Duplicate 
     * 
    */
    const duplicateItem = (index) => {
        const newitems = [...items.slice(0, index), { ...items[index] }, ...items.slice(index)];
        setAttributes({ items: newitems });

    }
    return <>
        <SelectControl label={__('Content Type', 'lightbox')} labelPosition="side" value={type} options={contentType} onChange={val => {
            const newItems = produce(items, draft => {
                draft[index]['type'] = val;
                draft[index]['content'] = 'content' === val ? '<!-- wp:paragraph --><!-- /wp:paragraph -->' : '';
            });
            setAttributes({ items: newItems });
        }} __nextHasNoMarginBottom />

        {/* Check than add thumbnail or button  */}
        {'image' !== type && 'gallery' === lightboxType && <>
            <Label>{__('Thumbnail:', 'lightbox')}</Label>
            <InlineMediaUpload value={thumbnail} type={['image']} onChange={val => updateItem('thumbnail', index, val)} placeholder={__('Enter Image URL', 'lightbox')} />
        </>}

        {/* Image  */}
        {'image' === type && <>
            <Label>{__('Image:', 'lightbox')}</Label>
            <InlineMediaUpload value={content} type={['image']} onChange={val => updateItem('content', index, val)} placeholder={__('Enter Image URL', 'lightbox')} />
            <Label className='mt10'>{__('Caption', 'lightbox')}</Label>
            <TextControl className="mt10" value={caption} onChange={val => {
                updateItem('caption', index, val)
            }} />

        </>}

        {/* Audio  */}
        {'audio' === type && <>
            <Label>{__('Audio:', 'lightbox')}</Label>
            <InlineMediaUpload value={content} type={['audio']} onChange={val => updateItem('content', index, val)} placeholder={__('Enter Audio URL', 'lightbox')} />
            <Label className='mt10'>{__('Caption', 'lightbox')}</Label>
            <TextControl className="mt10" value={caption} onChange={val => {
                updateItem('caption', index, val)
            }} />
        </>}

        {/* Video  */}
        {'video' === type && <>
            <Label>{__('Video:', 'lightbox')}</Label>
            <InlineMediaUpload value={content} type={['video']} onChange={val => updateItem('content', index, val)} placeholder={__('Enter Video URL', 'lightbox')} />
            <Label className='mt10'>{__('Caption', 'lightbox')}</Label>
            <TextControl className="mt10" value={caption} onChange={val => {
                updateItem('caption', index, val)
            }} />
        </>}

        {/* pdf  */}
        {/* {'pdf' === type && <>
            <Label>{__('Pdf:', 'lightbox')}</Label>
            <InlineMediaUpload value={content} type={['video']} onChange={val => updateItem('content', index, val)} placeholder={__('Enter Pdf URL', 'lightbox')} />
            <Label className='mt10'>{__('Caption', 'lightbox')}</Label>
            <TextControl className="mt10" value={caption} onChange={val => {
                updateItem('caption', index, val)
            }} />
        </>}

        {'iframe' === type && <>
            <Label>{__('Iframe:', 'lightbox')}</Label>
            <InlineMediaUpload value={content} type={['video']} onChange={val => updateItem('content', index, val)} placeholder={__('Iframe Map URL', 'lightbox')} />
            <Label className='mt10'>{__('Caption', 'lightbox')}</Label>
            <TextControl className="mt10" value={caption} onChange={val => {
                updateItem('caption', index, val)
            }} />
        </>} */}

        {/* Duplicate and Remove Button  */}
        {'button' !== lightboxType && <PanelRow className='itemAction mt20'>
            {1 < items.length && <Button className='removeItem' onClick={() => removeItem(index)} ><Dashicon icon='no' size={18} />{__('Remove', 'lightbox')}</Button>}
            <Button className='duplicateItem' onClick={() => duplicateItem(index)} >{icons.gearSettings}{__('Duplicate', 'lightbox')}</Button>
        </PanelRow>}

    </>
}
export default ItemSettings;